
import FooterChat from '../layouts/footerChat/index.vue'
import gallery from '@/components/gallery/index.vue'
import ChatsContainer from '@/components/main/components/ChatsContainer.vue';
import ContactsContainer from '@/components/main/components/ContactsContainer.vue';
import SettingsContainer from '@/components/main/components/SettingsContainer.vue';
import {mapState} from 'vuex'

export default {
  name: "mainWrapper",
  components: {
    FooterChat,
    gallery,
    ChatsContainer,
    ContactsContainer,
    SettingsContainer,
  },
  data: function () {

    return {
      prevRoute: null,
      loading: false,
      page: this.$route.query.page ? this.$route.query.page : 'chats',
      showPage:
        this.$route.path === '/chat' ||
        this.$route.path === '/contact' ||
        this.$route.path === '/publicPreview' ||
        this.$route.path === '/chatSettings' ||
        this.$route.path === '/chatInfo' ||
        this.$route.path === '/teamRoom' ||
        this.$route.path === '/invite',
        minimized: this.$store.state.mobile,
    }

  },

  created: () => {

  },

  watch: {
    '$route.query.page': {
      handler: function (page) {
        this.page = page ? page : "chats";
  },
      deep: true,
      immediate: true,
    },
    '$route.path': {
      handler: function (path) {
        this.showPage =
          path === '/chat' ||
          path === '/contact' ||
          path === '/publicPreview' ||
          path === '/chatSettings' ||
          path === '/chatInfo' ||
          path === '/teamRoom' ||
          path === '/invite';
      },
      deep: true,
      immediate: true,
    },
  },

  mounted() {
    this.page = this.$route.query.page ? this.$route.query.page : "chats";
    this.showPage =
      this.$route.path === '/chat' ||
      this.$route.path === '/contact' ||
      this.$route.path === '/publicPreview' ||
      this.$route.path === '/chatSettings' ||
      this.$route.path === '/chatInfo' ||
      this.$route.path === '/teamRoom' ||
      this.$route.path === '/invite';
  },

  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.prevRoute = from
    })
  },

  beforeRouteLeave(to, from, next) {
		this.$store.commit('setmodal', null)
		next()
	},

  computed: mapState({
    auth: state => state.auth,
    pocketnet: (state) => state.pocketnet,

    ...mapState([
      'currentUserChat',
      'minimized',
      'gallery',
      'active',
      'mobile',
      'recipientsTotal',
      'recipients'
    ]),

    showFooter : function(){
      return this.$route.name != 'chat' || this.minimized
    },

    showChats() {
      return this.pocketnet && this.$route.name !== 'chats' ? false : true;
    },
  }),

  methods: {
    closeGallery : function(){
      this.$store.commit('GALLERY', null)
    },
    sendMassMessage(chatMassMessage){

      const {message, recipients, total} = chatMassMessage;

      this.$store.commit('PROCESS_MASS_MAILING', true);
      this.$store.commit('SET_RECIPIENTS_TOTAL', total);

      this.core.user.usersInfo(recipients, true).then(async usersInfo => {                      
                              
        const sleep = async () => {

            return new Promise(resolve => {

              setTimeout(() => {

                resolve('slept')
                return true;
                
              }, 200)

            })

        }

        const sent = () => {

          recipients.shift();

          this.$store.commit('SET_RECIPIENTS', recipients)

          localStorage.setItem('chat_mass_message', JSON.stringify(chatMassMessage))        
        
        }

        
        for (const info of usersInfo){

            const id = this.core.mtrx.kit.tetatetid(info, this.core.user.userinfo)

            const matrixId = this.core.user.matrixId(info.id);
            
            var initialstate = [{
              "type": "m.set.encrypted",
              "state_key": "",
              "content": {
                  encrypted: true
              }
            }]


            const room = this.$store.state.chatsMap[id];

            if (room){

              console.log('room', room);
              await sleep()
              sent();
              const result = await this.core.mtrx.testmessage({room: room.roomId, message: message})
              console.log('result message', result);
                

            } else {

              sent();

              this.core.mtrx.client.createRoom(
                  {
                  room_alias_name: id,
                  visibility: 'private',
                  invite: [matrixId],
                  name: '#' + id,
                  initial_state: initialstate
              
                  }
              ).then(async(_chat) => {

                const result = await this.core.mtrx.testmessage({room: _chat.room_id, message: message})

                console.log('result message 2', result);

              }).catch(err => {
                console.log('room create mailing err', err);
              })
              

          }




        }

      })
    },

  },

  mounted(){

    const chatMassMessage = JSON.parse(localStorage.getItem('chat_mass_message') || '{}');

    if (chatMassMessage.recipients?.length && chatMassMessage.message){

      this.$store.commit('SET_RECIPIENTS', chatMassMessage.recipients)
      this.$store.commit('SET_RECIPIENTS_TOTAL', chatMassMessage.total)

    }
  }


}